import React, { useState } from 'react';
import './SearchContainer.css'
import './SearchCardType.css'
import Modal from "react-modal";

Modal.setAppElement('#root')

const InputSearchConditions = (props) => {
  const [modalCardTypeIsOpen, setModalCardTypeIsOpen] = useState(false);

  const handleCardNameChange = (e) => {
    props.setSearchConditions({ ...props.searchConditions, "cardName": e.target.value })
  }

  const handleIdColorChange = (e) => {
    let idColor = props.searchConditions.idColor || "";
    if (e.target.checked) idColor += e.target.value
    else idColor = idColor.toString().replace(e.target.value, "")
    props.setSearchConditions({ ...props.searchConditions, "idColor": idColor })
  }

  const handleCardTypeChange = (e) => {
    let cardType = props.searchConditions.cardType || [];
    if (e.target.checked) cardType.push(e.target.value)
    else cardType = cardType.filter(value => value !== e.target.value)
    props.setSearchConditions({ ...props.searchConditions, "cardType": cardType })
  }

  return (
    <div className="searchContainer">
      <div className="search_title">カード名</div>
      <div className="search_item"><input type="search" value={props.searchConditions.searchWord} onChange={e => handleCardNameChange(e)} /></div>

      <div className="search_title">固有色</div>
      <div className="search_item">
        <input type="checkbox" name="idColor" value="W" onChange={e => handleIdColorChange(e)}></input>白
        <input type="checkbox" name="idColor" value="U" onChange={e => handleIdColorChange(e)}></input>青
        <input type="checkbox" name="idColor" value="B" onChange={e => handleIdColorChange(e)}></input>黒
        <input type="checkbox" name="idColor" value="R" onChange={e => handleIdColorChange(e)}></input>赤
        <input type="checkbox" name="idColor" value="G" onChange={e => handleIdColorChange(e)}></input>緑
        <input type="checkbox" name="idColor" value="C" onChange={e => handleIdColorChange(e)}></input>無色
      </div>

      <div className="search_title">カードタイプ</div>
      <div className="search_item">
        <button onClick={() => setModalCardTypeIsOpen(true)}>別画面で選択</button>
        <Modal isOpen={modalCardTypeIsOpen} className="modal">
          <CardConditions
            handleCardTypeChange={e => handleCardTypeChange(e)}
            clilckCloseModal={() => setModalCardTypeIsOpen(false)}
            searchedCardType={props.searchConditions.cardType}
          />
        </Modal>
      </div>
    </div>

  )
}

const CardConditions = (props) => {
  let searchedCardType = props.searchedCardType || [];

  return (
    <div className="wrap">
      <div>カードタイプ</div>
      <div><input type="checkbox" name="cardType" value="creature" checked={searchedCardType.includes("creature")} onChange={e => props.handleCardTypeChange(e)}></input>クリーチャー</div>
      <div><input type="checkbox" name="cardType" value="artifact" checked={searchedCardType.includes("artifact")} onChange={e => props.handleCardTypeChange(e)}></input>アーティファクト</div>
      <div><input type="checkbox" name="cardType" value="sorcery" checked={searchedCardType.includes("sorcery")} onChange={e => props.handleCardTypeChange(e)}></input>ソーサリー</div>
      <div><input type="checkbox" name="cardType" value="instant" checked={searchedCardType.includes("instant")} onChange={e => props.handleCardTypeChange(e)}></input>インスタント</div>
      <div><input type="checkbox" name="cardType" value="enchant" checked={searchedCardType.includes("enchant")} onChange={e => props.handleCardTypeChange(e)}></input>エンチャント</div>
      <div><input type="checkbox" name="cardType" value="planeswalker" checked={searchedCardType.includes("planeswalker")} onChange={e => props.handleCardTypeChange(e)}></input>プレインズウォーカー</div>
      <div><input type="checkbox" name="cardType" value="land" checked={searchedCardType.includes("land")} onChange={e => props.handleCardTypeChange(e)}></input>土地</div>
      <div><input type="checkbox" name="cardType" value="legendary" checked={searchedCardType.includes("legendary")} onChange={e => props.handleCardTypeChange(e)}></input>伝説</div>
      <div><button onClick={() => props.clilckCloseModal()}>閉じる</button></div>
    </div>
  )
}

export default InputSearchConditions;